<template>
  <div class="customerdetails container">
  	<router-link to="/" class="btn btn-light">返回</router-link>
    <h1 class="page-header">
    	{{customer.name}}

    	<span class="float-right">
    		<router-link class="btn btn-primary" v-bind:to="'/edit/'+customer.id" >编辑</router-link>
    		<button class="btn btn-danger" v-on:click="deleteCustomer(customer.id)">删除</button>
    	</span>
    </h1>
    <ul class="list-group">
    	<li class="list-group-item">
    		<span class="glyphicon glyphicon-phone">{{customer.phone}}</span> <!-- glyphicon glyphicon-phone是bootstrap3.3.7的图标 -->
    	</li>
    	<li class="list-group-item">
    		<span class="glyphicon glyphicon-envelope">{{customer.email}}</span>
    	</li>
    </ul>

    <ul class="list-group">
    	<li class="list-group-item">
    		<span class="glyphicon glyphicon-phone">{{customer.education}}</span>
    	</li>
    	<li class="list-group-item">
    		<span class="glyphicon glyphicon-envelope">{{customer.graduateschool}}</span>
    	</li>
    	<li class="list-group-item">
    		<span class="glyphicon glyphicon-phone">{{customer.profession}}</span>
    	</li>
    	<li class="list-group-item">
    		<span class="glyphicon glyphicon-envelope">{{customer.profile}}</span>
    	</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'customerdetails',
  data () {
    return {
    	customer:"",
    }
  },
  methods:{
  	fetchCustomers(id){
  		this.$http.get("http://localhost:3000/users/"+id).then(
  		(response)=>{
  			// console.log(response);
  			this.customer=response.body;
  		})
  	},
  	deleteCustomer(id){
  		// console.log(id);
  		this.$http.delete("http://localhost:3000/users/"+id).then(function(response){
  			this.$router.push({path:"/",query:{alert:"用户信息删除成功！"}});
  		})
  	}
  },
  created(){
  	this.fetchCustomers(this.$route.params.id);
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



